/* Forms */

.input-group {
  .input-group-append,
  .input-group-prepend {
    background: $input-bg;
    color: $input-placeholder-color;
    width: auto;
    border: 0;
    .input-group-text {
      background: transparent;
      border: 0;
    }
  }
  .input-group-prepend {
    margin-right: 0;
    border-right: 1px solid $border-color;    
  }
  .input-group-append {
    margin-left: 0;
    border-left: 1px solid $border-color;
  }
}

.form-control {
  font-family: $type1-regular;
  font-size: $input-font-size;
  padding: $input-padding-y $input-padding-x;
  line-height: 14px;
  border: 0;
  &.form-control-lg {
    padding: $input-padding-y-lg $input-padding-x-lg;
  }
  &.form-control-sm {
    padding: $input-padding-y-sm $input-padding-x-sm;
  }
}

select {
  &.form-control {
    padding: .4375rem .75rem;
  }
}
.form-group {
  label {
    font-size: $default-font-size;
    line-height: 1;
    vertical-align: top;
  }
  &.has-danger {
    .form-control {
      border-color: theme-color(danger);
    }
  }
  .file-upload-default {
    visibility: hidden;
    position: absolute;
  }
  .file-upload-info {
    @extend .form-control;
  }
}
