/* Cards */

.card {
  border: 0;
  @include border-radius(2px);
  .card-body {
    padding: $card-padding-y $card-padding-x;
    + .card-body {
      padding-top: 0;
    }
  }
  &.card-outline-success{
		border: 1px solid theme-color("success");
	}
	&.card-outline-primary{
		border: 1px solid theme-color("primary");
	}
	&.card-outline-warning{
		border: 1px solid theme-color("warning");
	}
	&.card-outline-danger{
		border: 1px solid theme-color("danger");
	}
	&.card-rounded{
		@include border-radius(5px);
	}

  &.card-faded {
    background: #b5b0b2;
    border-color: #b5b0b2;
  }
  &.card-circle-progress {
    color: $white;
    text-align: center;
  }
  &.card-event {
    border: 0;
    color: color(white);
    .card-header {
      background: transparent;
      text-align: right;
      padding: 1.875rem .875rem;
      border-bottom: 0;
      i {
        padding: 0 .4375rem;
        &:before {
          font-size: 1.125rem;
        }
      }
    }
    .event-date {
      margin-bottom: .5rem;
    }
    .event-title {
      font-size: 1rem;
    }
  }
  &.card-weather {
    border: 0;
    color: color(white);
    .card-body {
      padding-top: 3.25rem;
      padding-bottom: 2.5rem;
      .temperature {
        font-family: $type1-semibold;
        font-size: 3.125rem;
        line-height: 1;
      }
      .day {
        font-family: $type1-bold;
        font-size: 2.188rem;
        line-height: 1;
      }
      .weather-text {
        font-family: $type1-regular;
        font-size: 1rem;
        line-height: 1;
        margin-bottom: .625rem;
      }
    }
  }
  &.card-activity-stat {
    border: 0;
    .card-img-overlay {
      padding: 2rem 1.5rem;
      .card-title {
        color: color(white);
      }
    }
    .card-body {
      position: relative;
      height: 260px;
      padding: 0;
      .card-inner-wrapper {
        position: absolute;
        top: -40px;
        width: 100%;
        padding: 0 $card-padding-x;
      }
      .card-inner {
        border: 1px solid $border-color;
        .card-body {
          height: auto;
          padding: 1rem .8rem;
          position: static;
          border: 0;
          .status-icon {
            font-size: 1.563rem;
          }
          .status {
            font-family: $type1-semibold;
            margin-bottom: 0;
          }
          .count {
            font-size: .8125rem;
            margin-bottom: 0;
          }
        }
      }
    }
  }
  &.card-growth-stat {
    background: url("../images/samples/growth_bg.jpg") top left no-repeat;
    background-size: cover;
    .growth-stat-content {
      .card-title {
        color: color(white);
        font-family: $type1-regular;
        text-decoration: underline;
        line-height: 1.56;
        text-transform: none;
      }
      .bold-text {
        font-size: 1.125rem;
      }
      i {
        &:before {
          font-size: 1.563rem;
          font-weight: 900;
        }
      }
    }
  }
  &.card-revenue {
    background: url("../images/samples/chart-bg.jpg");
    background-size: cover;
    color: color(white);
    .highlight-text {
      font-size: 1.875rem;
      font-family: $type1-bold;
    }
    .badge {
      background-color: rgba(color(white), .2);
      font-size: 1.125rem;
      padding: .5rem 1.25rem;
    }
  }
  &.card-revenue-table {
    .revenue-item {
      border-bottom: 1px solid $border-color;
      @extend .py-3;
      &:last-child {
        border-bottom: 0;
        @extend .pb-0;
      }
      &:first-child {
        @extend .pt-0;
      }
      .revenue-desc {
        margin-right: auto;
        width: 80%;
        p {
          margin-bottom: 0;
        }
      }
      .revenue-amount {
        margin-left: auto;
        width: 40%;
        p {
          font-size: 1.25rem;
          font-family: $type1-bold;
          text-align: right;
          .rtl & {
            text-align: left;
          }
        }
      }
    }
  }
  &.card-profile-preview {
    .card-img-top {
      height: 245px;
    }
    .card-img-overlay {
      height: 245px;
    }
    .profile-preview-rounded {
      img {
        width: 85px;
        height: 85px;
        border-radius: 100%;
      }
    }
    p {
      line-height: 27px;
    }
    .profile-status {
      border-right: 1px solid $border-color;
      &:last-child {
        border-right: 0;
      }
    }
  }
}

@each $color, $value in $theme-colors {
  .card-inverse-#{$color} {
    @include card-inverse-variant(rgba(theme-color($color), .2), theme-color-level($color, 1), theme-color-level($color, 3));
  }
}
