/* Sidebar */

.sidebar {
  min-height: calc(100vh - #{$navbar-height});
  background: $sidebar-light-bg;
  font-family: $type1-regular;
  padding: 1rem 0 0 0;
  width: $sidebar-width-lg;
  z-index: 11;
  transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -webkit-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -moz-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
  -ms-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;

  .nav {
    overflow: hidden;
    flex-wrap: nowrap;
    flex-direction: column;

    .nav-item {
      padding: .25rem 1.5rem;
            
      .collapse {
        z-index: 999;
      }

      .nav-link {
        align-items: center;
        display: flex;
        padding: $sidebar-menu-padding;
        white-space: nowrap;
        height: $nav-link-height;
        color: $sidebar-light-menu-color;

        .menu-icon {
          margin-right: 1.25rem;
          font-size: $sidebar-icon-font-size;
          line-height: 1;
          vertical-align: text-top;
          color: $sidebar-light-icon-color;
          &:before {
            vertical-align: top;
          }
        }

        .menu-arrow {
          margin-left: auto;
          color: $sidebar-light-arrow-color;
          &:before {
            display: inline-block;
            font: normal normal normal 20px/1 "Material Design Icons";
            font-size: $sidebar-arrow-font-size;
            text-rendering: auto;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            content: "\F35F";
          }
        }

        &[aria-expanded="true"] {
          .menu-arrow {
            &:before {
              content: "\F35E";
            }
          }
        }

        .menu-title {
          color: inherit;
          display: inline-block;
          font-size: $sidebar-menu-font-size;
          line-height: 1;
          vertical-align: middle;
        }

        .badge {
          margin-left: auto;
        }

        &:hover {
          color: $sidebar-light-menu-hover-color;
        }
      }

      &.nav-profile {
        .nav-link {
          height: auto;
          cursor: default;
          @include justify-content(center);
          .nav-profile-image {
            cursor: pointer;
            position: relative;
            width: 70px;
            height: 70px;
            img {
              width: 70px;
              height: 70px;
              border-radius: 100%;
              border: 1px solid $border-color;
              padding: 6px;
            }
            .login-status {
              position: absolute;
              width: 8px;
              height: 8px;
              border-radius: 100%;
              top: 8px;
              right: 5px;
              &.online {
                background: theme-color(success);
              }
              &.offline {
                background: theme-color(danger);
              }
              &.busy {
                background: theme-color(warning);
              }
            }
          }
        }
      }

      &.nav-category {
        .nav-link {
          font-family: $type1-bold;
          font-size: $sidebar-category-font-size;
          padding: $sidebar-category-padding;
          color: $sidebar-light-category-color;
          height: auto;
        }
      }

      &.active {
        > .nav-link {
          background: $sidebar-light-menu-active-bg;
          color: $sidebar-light-menu-active-color;
          .menu-title,
          i {
            color: $sidebar-light-menu-active-color;
          }
        }
      }
    }
    &:not(.sub-menu) {
      > .nav-item {
        &:not(.nav-category) {
          &:not(.nav-profile) { 
            &:hover {
              &:not(.active) {
                > .nav-link {
                  background: $sidebar-light-menu-hover-bg;
                  color: $sidebar-light-menu-hover-color;
                }
              }
            }
          }
        }
      }
    }
    &.sub-menu {
      margin-bottom: 0;
      padding: $sidebar-submenu-padding;

      .nav-item {
        padding: 0;
        .nav-link {
          color: $sidebar-light-submenu-color;
          padding: $sidebar-submenu-item-padding;
          font-size: $sidebar-submenu-font-size;
          line-height: 1;
          height: auto;
          position: relative;

          &.active {
            color: $sidebar-light-menu-active-color;
            background: transparent;
            &:before {
              background: $sidebar-light-menu-active-color;
            }
          }
          &:before {
            content: "";
            position: absolute;
            width: 7px;
            height: 7px;
            background: $sidebar-light-icon-color;
            border-radius: 50%;
            margin-left: -2rem;
            .rtl & {
              margin-left: auto;
              margin-right: -1rem;
            }
            top: 15px;
          }
        }

        &:hover {
          > .nav-link {
						&:not(.active) {
							background: $sidebar-light-submenu-hover-bg;
							color: $sidebar-light-submenu-hover-color;
							&:before {
								background: $sidebar-light-submenu-hover-color;
							}
						}
          }
        }
      }
    }
  }
}
//sidebar color variation
.sidebar-dark {
  .sidebar {
    background: $sidebar-dark-bg;
    .nav {
      .nav-item {
        .nav-link {
          color: $sidebar-dark-menu-color;
          .menu-title,
          i {
            color: inherit;
					}
        }
				&.nav-profile {
					.nav-profile-image {
						img {
							border-color: rgba($white, .2);
						}
					}
				}
        &.active {
          > .nav-link {
            background: $sidebar-dark-menu-active-bg;
            color: $sidebar-dark-menu-active-color;
            .menu-title,
            i {
              color: inherit;
            }
          }
        }
      }
      &:not(.sub-menu) {
        > .nav-item {
          &:not(.nav-category) {
            &:not(.nav-profile) { 
              &:hover {
                &:not(.active) {
                  > .nav-link {
                    background: $sidebar-dark-menu-hover-bg;
                    color: $sidebar-dark-menu-hover-color;
                  }
                }
              }
            }
          }
        }
      }
      &.sub-menu {
        .nav-item {
          .nav-link {
            color: $sidebar-dark-submenu-color;
            &.active {
              color: $sidebar-dark-menu-active-color;
              &:before {
                border-color: $sidebar-dark-menu-active-color;
              }
            }
          }
          &:hover {
            > .nav-link {
							&:not(.active) {
								color: $sidebar-dark-submenu-hover-color;
								&:before {
									background: $sidebar-dark-submenu-hover-color;
								}
							}
            }
          }
        }
      }
    }
  }
}
/* style for off-canvas menu*/
@media screen and (max-width: 991px) {
  .sidebar-offcanvas {
    position: fixed;
    max-height: calc(100vh - #{$navbar-height});
    top: $navbar-height;
    bottom: 0;
    overflow: auto;
    right: -$sidebar-width-lg;
    -webkit-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    &.active {
      right: 0;
    }
  }
}