/* Navbar */

.navbar {
  font-family: $type1-semibold;
  background: theme-color(primary);
  transition:background $action-transition-duration $action-transition-timing-function;
  -webkit-transition:background $action-transition-duration $action-transition-timing-function;
  -moz-transition:background $action-transition-duration $action-transition-timing-function;
  -ms-transition:background $action-transition-duration $action-transition-timing-function;
  .navbar-brand-wrapper {
    transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -webkit-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -moz-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    -ms-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;
    width: $sidebar-width-lg;
    height: $navbar-height;
    background: $white;

    .navbar-brand {
      color: $white;
      font-size: 1.5rem;
      line-height: 48px;
      margin-right: 0;
      padding: .25rem 0;

      &:active,
      &:focus,
      &:hover {
        color: lighten(color(gray-dark), 10%);
      }

      img {
        width: calc(#{$sidebar-width-lg} - 130px );
        max-width: 100%;
        height: 27px;
        margin: auto;
        vertical-align: bottom;
      }
    }

    .brand-logo-mini {
      display: none;
      img {
        width: calc(#{$sidebar-width-icon} - 50px );
        max-width: 100%;
        height: 35px;
        margin: auto;
      }
    }
  }

  .navbar-menu-wrapper {
    transition: width $action-transition-duration $action-transition-timing-function;
    -webkit-transition: width $action-transition-duration $action-transition-timing-function;
    -moz-transition: width $action-transition-duration $action-transition-timing-function;
    -ms-transition: width $action-transition-duration $action-transition-timing-function;
    color: $white;
    padding-left: 15px;
    padding-right: 15px;
    width: calc(100% - #{$sidebar-width-lg});
    height: $navbar-height;
    @media (max-width: 991px) {
      width: auto;
    }

    .navbar-toggler {
      border: 0;
      color: inherit;
      &:not(.navbar-toggler-right) {
        @media (max-width: 991px) {
          display: none;
        }
      }
      i {
        font-size: 1.563rem;
      }
    }

    .navbar-text {
      font-size: $default-font-size;
    }

    .navbar-nav {
      @include flex-direction(row);
      @include align-items(stretch);
      padding: 1.25rem 0;
      .nav-item {
        @include display-flex();
        @include align-items(center);
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        @media (max-width: 767px) {
          padding-right: .5rem;
          padding-left: .5rem;
        }
        border-right: 1px solid rgba($white, .2);
        &:last-child {
          border-right: 0;
        }
        .nav-link {
          color: inherit;
          font-size: $navbar-font-size;
          i {
            font-size: $navbar-icon-font-size;
          }
        }
        &.dropdown {
          .dropdown-toggle {
            padding: 0;
            &:after {
              content: "\F35D";
              display: inline-block;
              font: normal normal normal 24px/1 "Material Design Icons";
              font-size: 1.25rem;
              text-rendering: auto;
              line-height: 1px;
              -webkit-font-smoothing: antialiased;
              border: 0;
              vertical-align: middle;
            }
          }
          .count-indicator {
            position: relative;
            border-radius: 100%;
            text-align: center;
            .count {
              position: absolute;
              left: 17px;
              width: 6px;
              height: 6px;
              border-radius: 100%;
              top: 9px;
              background: theme-color(danger);
            }
            &:after {
              display: none;
            }
          }
          .navbar-dropdown {
            font-size: 0.9rem;
            margin-top: 0;
            position: absolute;
            top: calc(#{$navbar-height} - 20px );
            right: 0;
            left: auto;
            .rtl & {
              right: auto;
              left: 0;
            }
            &.dropdown-left {
              left: 0;
              right: auto;
            }
            min-width: 100%;
            -webkit-box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            -moz-box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            box-shadow: 0px 0px 13px -3px rgba(0,0,0,0.10);
            @include border-radius(4px);
            @media (max-width: 991px) {
              right: -85px;
            }
            @extend .dropdownAnimation;
            border: 1px solid rgba(182, 182, 182, 0.1);
            padding: 0;
            @include border-radius(0);
            overflow: hidden;
            .dropdown-item {
              @extend .d-flex;
              @extend .align-items-center;
              border-bottom: 1px solid lighten(color(gray-light),30%);
              margin-bottom: 0;
              padding: 11px 30px;
              line-height: 1.4;

              i {
                font-size: 17px;
              }

              .ellipsis {
                max-width: 200px;
                overflow: hidden;
                text-overflow: ellipsis;
              }

              .preview-icon {
                width: 40px;
                height: 40px;
              }
              .small-text {
                font-size: .75rem;
              }
              .badge {
                margin-bottom: .5rem;
              }
            }

            .dropdown-divider {
              margin: 0;
            }
            &.navbar-dropdown-large {
              width: 300px;
              .dropdown-item {
                white-space: normal;              
              }
            }
          }
        }
        &.nav-search {
          .input-group {
            width: 260px;
            .input-group-prepend {
              background: transparent;
              .input-group-text {
                padding: 0;              
              }
            }
            .form-control,
            .input-group-text {
              background: transparent;
              color: $white;              
              border: 0;
            }
            .form-control {
              padding-left: .5rem;
              font-family: $type1-semibold;
              @include input-placeholder{
                color: rgba($white, .5);
              }
            }
          }
        }
      }
      &.navbar-nav-right {
        .nav-item {
          border: 0;
        }
        @media (min-width: 992px) {
          margin-left: auto;
          .rtl & {
            margin-left: 0;
            margin-right: auto;
          }
        }
      }
    }
  }
  //navbar color variations
  &.navbar-dark {
    background: $black;
    .navbar-toggler {
      color: $white;
    }
    .count{
      border-color: theme-color(dark);
    }
  }

  &.navbar-light {
    background: theme-color(light);
    .navbar-menu-wrapper {
      .navbar-toggler {
        color: theme-color(primary);
      }
      .navbar-nav {
        .nav-item {
          .nav-link {
            color: theme-color(primary);
            .input-group {
              .input-group-prepend {
                .input-group-text {
                  color: theme-color(primary);
                }
              }
              .form-control {
                @include input-placeholder{
                  color: rgba(theme-color(primary), .8);
                }
              }
            }
          }
        }
      }
    }
  }

  &.navbar-success {
    background: theme-color(success);
  }

  &.navbar-danger {
    background: theme-color(danger);

    .navbar-menu-wrapper {
      .navbar-nav {
        .nav-item {
          .nav-link {
            &.count-indicator {
              .count {
                background: darken(theme-color(danger), 30%);
              }
            }
          }
        }
      }
    }
  }

  &.navbar-warning {
    background: theme-color(warning);
  }

  &.navbar-pink {
    background: color(pink);
  }

  &.navbar-info {
    background: theme-color(info);
  }
}
@media (max-width:991px) {
  .navbar {
    flex-direction: row;

    .navbar-brand-wrapper {
      width: 75px;

      .brand-logo {
        display: none;
      }

      .brand-logo-mini {
        display: inline-block;
      }
    }
  }

  .navbar-collapse {
    display: flex;
    margin-top: 0.5rem;
  }
}
