/* Typeahead */

.tt-menu,
.gist {
  text-align: left;
}

.twitter-typeahead {
  max-width: 100%;
}

.typeahead,
.tt-query,
.tt-hint {
  @extend .form-control;
}

.typeahead {
  &:focus {
    border-color: $border-color;
  }
}


.tt-query {
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
     -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}

.tt-hint {
  color: color(gray)
}

.tt-menu {
  width: 100%;
  margin: 0;
  padding: 8px 0;
  background-color: color(white);
  border: 1px solid $border-color;
  border-top: 0;
  border-radius: 0;
}

.tt-suggestion {
  padding: 3px 20px;
  font-size: $default-font-size;
  &:hover {
    cursor: pointer;
    color: color(white);
    background-color: theme-color(primary);
  }
  .tt-cursor {
    color: color(white);
    background-color: theme-color(primary);
  }
  p {
    margin: 0;
  }
}
