
////////// COLOR SYSTEM //////////

$blue:              #03a9f3;
$indigo:            #6610f2;
$purple:            #ab8ce4;
$pink:              #e8538e;
$red:               #f96868;
$orange:            #fb9678;
$yellow:            #ffb463;
$green:             #00c292;
$teal:              #58d8a3;
$cyan:              #57c7d4;
$black:             #000;
$white:             #ffffff;
$white-smoke:       #f4f4f4;
$ghost-white:       #f7fafc;
$violet:            #41478a;
$darkslategray :    #2e383e;
$dodger-blue :      #3498db;
$blue-teal-gradient: linear-gradient(88deg, #13b4ca, #18cabe);


$colors: (
  blue:             $blue,
  indigo:           $indigo,
  purple:           $purple,
  pink:             $pink,
  red:              $red,
  orange:           $orange,
  yellow:           $yellow,
  green:            $green,
  teal:             $teal,
  cyan:             $cyan,
  white:            $white,
  gray:             #434a54,
  gray-light:       #aab2bd,
  gray-lighter:     #e8eff4,
  gray-lightest:    #e6e9ed,
  gray-dark:        #0f1531,
  black:            #000000
);

$primary:       #1976d2;
$secondary:     #c5c5c5;
$success:       #16c115;
$info:          #491fc2;
$warning:       #fe9d14;
$danger:        #e50a2e;
$light:         $white;
$dark:          $gray-800 !default;


$theme-colors: (
  primary:         $primary,
  secondary:       $secondary,
  success:         $success,
  info:            $info,
  warning:         $warning,
  danger:          $danger,
  light:           $light,
  dark:            $dark
);

////////// COLOR SYSTEM //////////



////////// COLOR VARIABLES //////////

$content-bg: #F6F8FA;
$footer-bg: $content-bg;
$footer-color: color(dark);

////////// COLOR VARIABLES //////////



////////// SOCIAL COLORS //////////

$social-colors: (
  twitter: #1da1f2,
  facebook: #3b579d,
  google: #dc4a38,
  linkedin: #0177b4,
  pinterest: #cc2127,
  youtube: #e52d27,
  github: #333333,
  behance: #1769ff,
  dribbble: #ea4c89,
  reddit: #ff4500
);

////////// SOCIAL COLORS //////////

////////// FONTS //////////

$type1-light:    'opensans-light', sans-serif;
$type1-regular:  'opensans-regular', sans-serif;
$type1-semibold: 'opensans-semibold', sans-serif;
$type1-bold:     'opensans-bold', sans-serif;

$default-font-size: .875rem;
$text-muted: #a5a5a5;
$body-color: #444444;
$border-color: rgba(#989898, .2);

////////// FONT VARIABLES //////////

////////// SIDEBAR ////////
$sidebar-width-lg: 272px;
$sidebar-width-mini: 185px;
$sidebar-width-icon: 90px;

$sidebar-light-bg: $white;
$sidebar-light-menu-color: #aaaaaa;
$sidebar-light-menu-active-bg: inherit;
$sidebar-light-menu-active-color: theme-color(primary);
$sidebar-light-menu-hover-bg: #f6f6f6;
$sidebar-light-menu-hover-color: #8d9498;
$sidebar-light-submenu-color: #999999;
$sidebar-light-submenu-hover-bg: initial;
$sidebar-light-submenu-hover-color: #000;
$sidebar-light-category-color: #aaaaaa;
$sidebar-light-icon-color: #aaaaaa;
$sidebar-light-arrow-color: #989898;

$sidebar-dark-bg: $black;
$sidebar-dark-menu-color: #aaaaaa;
$sidebar-dark-menu-active-bg: inherit;
$sidebar-dark-menu-active-color: theme-color(primary);
$sidebar-dark-menu-hover-bg: rgba($white, .2);
$sidebar-dark-menu-hover-color: $white;
$sidebar-dark-submenu-color: $sidebar-dark-menu-color;
$sidebar-dark-submenu-hover-bg: initial;
$sidebar-dark-submenu-hover-color: $white;
$sidebar-dark-category-color: #aaaaaa;;

$sidebar-category-font-size: .75rem;
$sidebar-category-padding: .5rem 1.188rem;

$sidebar-menu-font-size: .8125rem;
$sidebar-menu-padding: .75rem 1rem;
$nav-link-height: 42px;

$sidebar-submenu-padding: .5rem 0 0 2.95rem;
$sidebar-submenu-font-size: .8125rem;
$sidebar-submenu-item-padding: .75rem .5rem;

$sidebar-icon-font-size: 1.25rem;
$sidebar-arrow-font-size: 1.25rem;

$sidebar-profile-bg: transparent;
$sidebar-profile-padding: 0rem 1.625rem 2.25rem 1.188rem;

$sidebar-mini-menu-padding: .8125rem 1rem .8125rem 1rem;

$sidebar-icon-only-menu-padding: .5rem 1.625rem .5rem 1.188rem;
$sidebar-icon-only-submenu-padding: 0 0 0 2rem;
$sidebar-icon-only-submenu-width: 200px;

$rtl-sidebar-submenu-padding: .5rem 1.25rem 0 0;

///////// SIDEBAR ////////

///////// SETTINGS PANEL ////////

$settings-panel-width: 300px;

///////// HORIZONTAL MENU /////////
$horizontal-menu-bg : $white;
$horizontal-menu-padding: 0 1rem;
$horizontal-menu-height:  57px;
$horizontal-menu-item-color: #202339;
$horizontal-menu-submenu-color: $horizontal-menu-item-color;
$horizontal-menu-font-size: .875rem;
$horizontal-menu-item-padding: .75rem 0;
$horizontal-menu-icon-font-size: .9375rem;
$horizontal-menu-submenu-item-padding: .85rem 0;
///////// HORIZONTAL MENU /////////

///////// NAVBAR ////////

$navbar-height: 58px;
$navbar-font-size: .875rem;
$navbar-icon-font-size: 1.563rem;
$navbar-item-bg: #F6F8FA;

///////// NAVBAR ////////



///////// BUTTONS ////////

$button-fixed-width:  130px;

$btn-padding-y:       .75rem;
$btn-padding-x:       2rem;
$btn-line-height:     1;

$btn-padding-y-xs:    .5rem;
$btn-padding-x-xs:    .75rem;

$btn-padding-y-sm:    .625rem;
$btn-padding-x-sm:    1rem;

$btn-padding-y-lg:    .94rem;
$btn-padding-x-lg:    2.5rem;

$btn-font-size:       .875rem;
$btn-font-size-xs:    .8125rem;
$btn-font-size-sm:    .875rem;
$btn-font-size-lg:    .875rem;

$btn-border-radius:         0;
$btn-border-radius-xs:      0;
$btn-border-radius-sm:      0;
$btn-border-radius-lg:      0;
$btn-border-width:          1px;

$social-btn-padding:        18px;
$social-btn-icon-size:      1rem;

///////// BUTTONS ////////



/////////  FORMS /////////

$input-bg:                #f7f7f7;
$input-border-radius:     0;
$input-border-radius-lg:  0;
$input-border-radius-sm:  0;
$input-border-color:      $border-color;

$input-focus-bg:                        $input-bg;
$input-focus-border-color:              $input-border-color;
$input-focus-color:                     $input-color;

$input-placeholder-color: #a6a6a6;

$input-font-size:         .75rem;
$input-padding-y:         .75rem;
$input-padding-x:         1.25rem;
$input-line-height:       1;

$input-padding-y-sm:      .625rem;
$input-padding-x-sm:      .81rem;
$input-line-height-sm:    1;

$input-padding-y-lg:      .94rem;
$input-padding-x-lg:      1.94rem;
$input-line-height-lg:    1;

///////// FORMS /////////

////////  DROPDOWNS ///////

$dropdown-border-color: $border-color;
$dropdown-divider-bg:   $border-color;
$dropdown-link-color:   $body-color;
$dropdown-header-color: $body-color;
////////  DROPDOWNS ///////

//////// TABLES ////////

$table-accent-bg: $content-bg;
$table-hover-bg:  $content-bg;
$table-cell-padding: 1rem .9375rem;
$table-border-color: $border-color;

$table-inverse-bg: #2a2b32;
$table-inverse-color: color(white);

//////// TABLES ////////



////////// MEASUREMENT AND PROPERTY VARIABLES //////////

$boxed-container-width: 96%;
$border-property: 1px solid $border-color;
$card-spacing-y: 1.875rem;
$card-padding-y: 1.88rem;
$card-padding-x: 1.81rem;
$grid-gutter-width: 20px;
$action-transition-duration: 0.25s;
$action-transition-timing-function: ease;
////////// OTHER VARIABLES //////////



////////// BREAD CRUMBS VARIABLES //////////

// default styles
$breadcrumb-padding-y:              0.56rem;
$breadcrumb-padding-x:              1.13rem;
$breadcrumb-item-padding:           .5rem;
$breadcrumb-margin-bottom:          1rem;
$breadcrumb-font-size:              $default-font-size;
$breadcrumb-bg:                     transparent;
$breadcrumb-border-color:           $border-color;
$breadcrumb-divider-color:          $gray-600;
$breadcrumb-active-color:           $gray-700;
$breadcrumb-border-width:           1px;

// custom styles
$breadcrumb-custom-padding-y:       0;
$breadcrumb-custom-padding-x:       0;
$breadcrumb-custom-item-padding-y:  0.56rem;
$breadcrumb-custom-item-padding-x:  10px;
$breadcrumb-custom-item-color:      $black;
$breadcrumb-item-bg:                #dbe3e6;

////////// BREAD CRUMBS VARIABLES //////////



////////// MODALS VARIABLES //////////

$modal-inner-padding:               15px;
$modal-dialog-margin:               10px;
$modal-dialog-margin-y-sm-up:       30px;
$modal-title-line-height:           $line-height-base;
$modal-content-bg:                  $content-bg;
$modal-content-box-shadow-xs:       0 3px 9px rgba($black,.5);
$modal-content-box-shadow-sm-up:    0 5px 15px rgba($black,.5);

$modal-backdrop-bg:                 $black;
$modal-backdrop-opacity:            .5;

$modal-header-border-color:         $border-color;
$modal-content-border-color:        $border-color;
$modal-footer-border-color:         $border-color;

$modal-header-border-width:         $border-width;
$modal-content-border-width:        $border-width;
$modal-footer-border-width:         $border-width;

$modal-header-padding-x:              26px;
$modal-header-padding-y:              25px;

$modal-body-padding-x:                26px;
$modal-body-padding-y:                35px;

$modal-footer-padding-x:              31px;
$modal-footer-padding-y:              15px;

$modal-lg:                          90%;
$modal-md:                          500px;
$modal-sm:                          300px;
$modal-transition:                  transform .4s ease;

////////// MODALS VARIABLES //////////


////////// TOOLTIP VARIABLES //////////

//default styles
$tooltip-font-size:     .75rem;
$tooltip-padding-y:     .4rem;
$tooltip-padding-x:     .75rem;
$tooltip-border-radius: .375rem;
$tooltip-bg:  theme-color(dark);
$tooltip-arrow-color: $tooltip-bg;

////////// TOOLTIP VARIABLES //////////



////////// POPOVER VARIABLES //////////

$popover-bg:       theme-color(dark);
$popover-border-color:    $border-color;
$popover-arrow-color:     $popover-bg;
$popover-arrow-outer-color: $popover-border-color;
$popover-header-bg:   $popover-bg;

////////// POPOVER VARIABLES //////////

////////// ACCORDION VARIABLES //////////
$accordion-bg:    $white;
$accordion-border-width: 1px;
$accordion-border-color: darken($accordion-bg, 10%);
////////// ACCORDION VARIABLES //////////